import { useState, useEffect } from 'react';
import { api } from '../../services/api';

interface Product {
  _id: string;
  vendor: string | { _id: string };
  name: string;
  description?: string;
  price: number;
  mrp?: number;
  category?: string;
  threshold?: number;
  image?: string;
  isAvailable: boolean;
  stock: number;
  createdAt?: string;
}

const emptyProduct: Partial<Product> = {
  name: '',
  description: '',
  price: 0,
  mrp: 0,
  category: '',
  threshold: 0,
  image: '',
  isAvailable: true,
  stock: 0,
};

export default function VendorProducts() {
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(false);
  const [mode, setMode] = useState<'list' | 'create' | 'edit'>('list');
  const [editingId, setEditingId] = useState<string | null>(null);
  const [formData, setFormData] = useState<Partial<Product>>(emptyProduct);
  const [vendorId, setVendorId] = useState<string>('');
  const [vendorCategory, setVendorCategory] = useState<string>('');

  useEffect(() => {
    // Get vendor ID from stored user data
    if (typeof window !== 'undefined') {
      try {
        const stored = localStorage.getItem('cc_user');
        if (stored) {
          const parsed = JSON.parse(stored);
          setVendorId(parsed.id);
        }
      } catch {}
    }
  }, []);

  // Load products and vendor meta when vendorId is set
  useEffect(() => {
    if (vendorId) {
      loadVendorMeta();
      loadProducts();
    }
  }, [vendorId]);

  const loadVendorMeta = async () => {
    try {
      const vendor = await api.vendors.getById(vendorId);
      setVendorCategory(vendor?.vendorType || vendor?.storeName || '');
    } catch (error) {
      console.error('Error loading vendor details:', error);
    }
  };

  const loadProducts = async () => {
    try {
      setLoading(true);
      const data = await api.products.getAll();
      // Filter products for current vendor
      const vendorProducts = data.filter((p: Product) => {
        if (!p.vendor) return false;
        // p.vendor can be either a string (ID) or an object (populated)
        const pVendorId = typeof p.vendor === 'object' ? (p.vendor as any)._id : p.vendor;
        return pVendorId === vendorId;
      });
      setProducts(vendorProducts);
    } catch (error) {
      console.error('Error loading products:', error);
      alert('Failed to load products');
    } finally {
      setLoading(false);
    }
  };

  const handleCreate = () => {
    setMode('create');
    setFormData({ ...emptyProduct, vendor: vendorId, category: vendorCategory });
    setEditingId(null);
  };

  const handleEdit = async (id: string) => {
    try {
      const product = await api.products.getById(id);
      setFormData(product);
      setEditingId(id);
      setMode('edit');
    } catch (error) {
      console.error('Error loading product:', error);
      alert('Failed to load product details');
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm('Are you sure you want to delete this product?')) return;
    try {
      await api.products.delete(id);
      alert('Product deleted successfully');
      loadProducts();
    } catch (error) {
      console.error('Error deleting product:', error);
      alert('Failed to delete product');
    }
  };

  const handleToggleAvailability = async (product: Product) => {
    try {
      await api.products.update(product._id, {
        ...product,
        isAvailable: !product.isAvailable,
      });
      loadProducts();
    } catch (error) {
      console.error('Error updating product:', error);
      alert('Failed to update product availability');
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      setLoading(true);
      const payload = {
        ...formData,
        vendor: vendorId,
        category: vendorCategory || formData.category,
      };
      if (mode === 'create') {
        await api.products.create(payload);
        alert('Product created successfully');
      } else {
        await api.products.update(editingId!, payload);
        alert('Product updated successfully');
      }
      setMode('list');
      loadProducts();
    } catch (error) {
      console.error('Error saving product:', error);
      alert('Failed to save product');
    } finally {
      setLoading(false);
    }
  };

  const handleCancel = () => {
    setMode('list');
    setFormData(emptyProduct);
    setEditingId(null);
  };

  const handleImageFile = (file: File | null) => {
    if (!file) {
      updateField('image', '');
      return;
    }
    const reader = new FileReader();
    reader.onload = () => {
      updateField('image', reader.result as string);
    };
    reader.readAsDataURL(file);
  };

  const updateField = (field: string, value: any) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  if (mode === 'list') {
    return (
      <>
        <div className="products-header">
          <h1 className="products-title">Inventory Management</h1>
          <button className="btn-primary" onClick={handleCreate}>
            Add New Product
          </button>
        </div>

        {loading ? (
          <div className="products-loading">Loading products...</div>
        ) : (
          <>
            <div className="products-stats">
              <div className="stat-card">
                <div className="stat-label">Total Products</div>
                <div className="stat-value">{products.length}</div>
              </div>
              <div className="stat-card">
                <div className="stat-label">Available</div>
                <div className="stat-value">{products.filter((p) => p.isAvailable).length}</div>
              </div>
              <div className="stat-card">
                <div className="stat-label">Out of Stock</div>
                <div className="stat-value">{products.filter((p) => p.stock === 0).length}</div>
              </div>
              <div className="stat-card">
                <div className="stat-label">Low Stock</div>
                <div className="stat-value">{products.filter((p) => p.stock > 0 && p.stock < 10).length}</div>
              </div>
            </div>

            <section className="products-section">
              <h2 className="section-title">Product List</h2>
              <div className="products-table-wrap">
                <table className="products-table">
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Category</th>
                      <th>Price</th>
                      <th>Stock</th>
                      <th>Status</th>
                      <th>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {products.map((product) => (
                      <tr key={product._id}>
                        <td>
                          <div className="product-name-cell">
                            {product.image && (
                              <img src={product.image} alt={product.name} className="product-thumb" />
                            )}
                            <div>
                              <div className="product-name">{product.name}</div>
                              {product.description && (
                                <div className="product-desc">{product.description.substring(0, 50)}...</div>
                              )}
                              {(product.mrp || product.threshold) && (
                                <div className="product-desc">
                                  {product.mrp ? `MRP: ₹${product.mrp.toFixed(2)}` : ''}
                                  {product.mrp && product.threshold ? ' • ' : ''}
                                  {product.threshold ? `Threshold: ${product.threshold}` : ''}
                                </div>
                              )}
                            </div>
                          </div>
                        </td>
                        <td>{vendorCategory || product.category || '—'}</td>
                        <td>₹{product.price.toFixed(2)}</td>
                        <td>
                          <span
                            className={`stock-badge ${
                              product.stock === 0 ? 'out' : product.stock < 10 ? 'low' : 'good'
                            }`}
                          >
                            {product.stock}
                          </span>
                        </td>
                        <td>
                          <span className={`status-badge ${product.isAvailable ? 'active' : 'inactive'}`}>
                            {product.isAvailable ? 'Available' : 'Disabled'}
                          </span>
                        </td>
                        <td>
                          <div className="action-btns">
                            <button className="btn-edit" onClick={() => handleEdit(product._id)}>
                              Edit
                            </button>
                            <button
                              className={`btn-toggle ${product.isAvailable ? 'disable' : 'enable'}`}
                              onClick={() => handleToggleAvailability(product)}
                            >
                              {product.isAvailable ? 'Disable' : 'Enable'}
                            </button>
                            <button className="btn-delete" onClick={() => handleDelete(product._id)}>
                              Delete
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))}
                    {products.length === 0 && (
                      <tr>
                        <td colSpan={6} className="empty-state">
                          No products found. Add your first product to get started!
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            </section>
          </>
        )}
      </>
    );
  }

  return (
    <>
      <div className="products-header">
        <h1 className="products-title">{mode === 'create' ? 'Add New Product' : 'Edit Product'}</h1>
        <button className="btn-secondary" onClick={handleCancel}>
          Back to List
        </button>
      </div>

      <form className="product-form" onSubmit={handleSubmit}>
        <div className="form-section">
          <h3 className="form-section-title">Product Information</h3>
          <div className="form-grid">
            <div className="form-field">
              <label>Product Name *</label>
              <input
                type="text"
                required
                value={formData.name || ''}
                onChange={(e) => updateField('name', e.target.value)}
              />
            </div>
            <div className="form-field">
              <label>Price (₹) *</label>
              <input
                type="number"
                required
                min="0"
                step="0.01"
                value={formData.price || ''}
                onChange={(e) => updateField('price', Number(e.target.value))}
              />
            </div>
            <div className="form-field">
              <label>MRP (₹)</label>
              <input
                type="number"
                min="0"
                step="0.01"
                value={formData.mrp || ''}
                onChange={(e) => updateField('mrp', Number(e.target.value))}
              />
            </div>
            <div className="form-field">
              <label>Stock Quantity *</label>
              <input
                type="number"
                required
                min="0"
                value={formData.stock || ''}
                onChange={(e) => updateField('stock', Number(e.target.value))}
              />
            </div>
            <div className="form-field">
              <label>Threshold</label>
              <input
                type="number"
                min="0"
                value={formData.threshold || ''}
                onChange={(e) => updateField('threshold', Number(e.target.value))}
              />
            </div>
            <div className="form-field">
              <label>Category (from vendor)</label>
              <input type="text" value={vendorCategory || 'Not set'} disabled />
            </div>
            <div className="form-field form-field-full">
              <label>Description</label>
              <textarea
                rows={3}
                value={formData.description || ''}
                onChange={(e) => updateField('description', e.target.value)}
              />
            </div>
            <div className="form-field form-field-full">
              <label>Product Image (optional)</label>
              <input
                type="file"
                accept="image/png, image/jpeg, image/jpg, image/webp"
                onChange={(e) => handleImageFile(e.target.files?.[0] || null)}
              />
              {formData.image && (
                <div className="image-preview">
                  <img src={formData.image} alt="Preview" />
                </div>
              )}
            </div>
            <div className="form-field form-field-checkbox">
              <label>
                <input
                  type="checkbox"
                  checked={formData.isAvailable || false}
                  onChange={(e) => updateField('isAvailable', e.target.checked)}
                />
                Available for Sale
              </label>
            </div>
          </div>
        </div>

        <div className="form-actions">
          <button type="button" className="btn-secondary" onClick={handleCancel}>
            Cancel
          </button>
          <button type="submit" className="btn-primary" disabled={loading}>
            {loading ? 'Saving...' : mode === 'create' ? 'Add Product' : 'Update Product'}
          </button>
        </div>
      </form>
    </>
  );
}
